(function() {
  angular.module('futurebytrump', ['ngAnimate', 'ngTouch', 'ngSanitize', 'ui.router', '720kb.socialshare']);

}).call(this);


(function() {
  angular.module('futurebytrump').controller('MainQuoteController', function($state, $stateParams, sentence) {
    this.sentence = sentence;
    this.url = $state.href('main.quote', $stateParams, {
      absolute: true
    });
    this.medias = ["Skiing Magazine", "Canton Cherokee Tribune", "WJXT TV", "Cartoon Network", "Daily Beast", "Parade Magazine", "TV Guide Magazine", "Cookeville Herald-Citizen", "The Way to the Pot", "Badass Touch", "Think Pig and Kiss Ass", "Blink Like a Champion", "Time to get Dough", "How to get Itch", "Tripped America", "The Rat of the Deal", "Brushing magazine"];
    this.media = this.medias[Math.floor(Math.random() * this.medias.length)];
    this.tomorrow = new Date(new Date().getTime() + 24 * 60 * 60 * 1000);
    this.nonBreaking = function(val) {
      return val.replace(/\s(\?|!|:)/g, ' $1');
    };
    return null;
  });

}).call(this);


(function() {
  angular.module('futurebytrump').controller('MainController', function($state, facts) {
    this.bg = 'trump-' + Math.floor(Math.random() * 4);
    this.generate = function() {
      var i, idx, key, p, params, results;
      this.bg = 'trump-' + Math.floor(Math.random() * 4);
      params = {
        p1: null,
        p2: null,
        p3: null,
        p4: null
      };
      results = [];
      while (params.p1 === params.p2 && params.p2 === params.p3 && params.p3 === params.p4) {
        for (p = i = 1; i <= 4; p = ++i) {
          key = 'p' + p;
          params[key] = null;
          while (params[key] === null) {
            idx = Math.floor(Math.random() * facts[key].length);
            if (facts[key][idx] !== '') {
              params[key] = idx;
            }
          }
        }
        results.push($state.go('main.quote', params));
      }
      return results;
    };
    return null;
  });

}).call(this);


(function() {
  angular.module('futurebytrump').run(function($rootScope, $location, $window) {
    $rootScope.appTitle = 'lorem ipsum';
    return $rootScope.$on("$stateChangeSuccess", function() {
      if ($window.ga != null) {
        return $window.ga('send', 'pageview', {
          page: $location.url()
        });
      }
    });
  });

}).call(this);


(function() {
  angular.module('futurebytrump').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $urlRouterProvider.otherwise('/');
    return $stateProvider.state('main', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainController',
      controllerAs: 'main',
      resolve: {
        facts: function($http) {
          return $http.get('assets/facts.json').then(function(r) {
            return r.data;
          });
        }
      }
    }).state('main.quote', {
      url: ':p1::p2::p3::p4',
      templateUrl: 'app/main/quote/quote.html',
      controller: 'MainQuoteController',
      controllerAs: 'quote',
      resolve: {
        sentence: function($stateParams, facts) {
          var p;
          p = $stateParams;
          return [facts.p1[p.p1], facts.p2[p.p2], facts.p3[p.p3], facts.p4[p.p4]].join('');
        }
      }
    });
  });

}).call(this);


(function() {
  angular.module('futurebytrump').constant('moment', moment);

}).call(this);


(function() {
  angular.module('futurebytrump').config(function($logProvider) {
    'ngInject';
    return $logProvider.debugEnabled(true);
  });

}).call(this);


angular.module("futurebytrump").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"main\"><img src=\"assets/images/flag.svg\" class=\"main__flag\"> <img class=\"main__bg\" ng-src=\"assets/images/{{ main.bg }}.jpg\"><div class=\"main__credit\">Satirical website by <a href=\"https://twitter.com/ThePixelHunt\" target=\"_blank\">ThePixelHunt</a> &amp; <a href=\"https://twitter.com/pirhoo\" target=\"_blank\">Pirhoo</a> | Photos: <a href=\"https://www.flickr.com/photos/gageskidmore/\" target=\"_blank\">Gage Skidmore</a> | <a href=\"https://docs.google.com/spreadsheets/d/1HcXyZeAbTpTFnl825CbEZ-POyhSNBaD-ZmabE-nUH88\" target=\"_blank\">Sources</a></div><div ui-view=\"\"><div class=\"main__hexagon\"></div><div class=\"lead main__lead text-center\"><h1>A Future by <span class=\"tricolor\">Donald Trump</span></h1><p class=\"lead\">Feeling a bit lost about&#8203;your future President\'s view on things? &#8203;Here\'s a selection of his&#8203;most inspirational quotes.</p><p class=\"lead\"><em>(we might have mixed them up a little bit, though, but you get the idea)</em></p><p class=\"main__lead__generate\"><button class=\"btn btn-primary btn-lg\" ng-click=\"main.generate()\">Get inspired by Trump!</button></p></div></div></div>");
$templateCache.put("app/main/quote/quote.html","<div class=\"main__quote text-center\"><p class=\"main__quote__sentence\">❝&nbsp;{{ quote.nonBreaking(quote.sentence) }}&nbsp;❞</p><p class=\"main__quote__meta\">{{ quote.tomorrow | date:\'dd/MM/yyyy\' }} — ❝{{ quote.media }}❞ — <span class=\"\"><button socialshare=\"\" socialshare-text=\"Trumper words were never spoken: {{ quote.url }} #futurebytrump\" socialshare-url=\"null\" socialshare-provider=\"twitter\" class=\"btn btn-outline btn-sm\"><i class=\"fa fa-fw fa-twitter\"></i> <span class=\"sr-only\">Twitter</span></button> <button socialshare=\"\" socialshare-text=\"« {{ quote.sentence }} »\" socialshare-url=\"{{ quote.url }}\" socialshare-provider=\"facebook\" socialshare-type=\"feed\" socialshare-via=\"596379203861701\" socialshare-description=\"With every passing day, Donald Trumps shares a little bit more wisdom with the world. But if you think he\'s not getting enough attention, here\'s your fix.\" socialshare-redirect-uri=\"http://www.futurebytrump.com/close.html\" class=\"btn btn-outline btn-sm\"><i class=\"fa fa-fw fa-facebook\"></i> <span class=\"sr-only\">Facebook</span></button></span></p><p class=\"main__quote__actions\"><button class=\"btn btn-primary btn-lg\" ng-click=\"main.generate()\">I want to be inspired again!</button></p></div>");}]);